import React, { Dispatch, useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { TextInputKeyPressEventData } from 'react-native';
import { NavigationProp, useNavigation } from '@react-navigation/native';
import { setUserData, unsetUserData } from 'reduxStore/slices/userSlice';
import { auth, signUp } from 'services/firebase';
import { SignUp } from 'screens/SignUp/signup';
import { AnyAction } from '@reduxjs/toolkit';

const SignupContainer = ({ children }: { children: React.FunctionComponent }) => {
  const [username, setUsername] = useState<string>('');
  const navigation = useNavigation<NavigationProp<any>>();
  const dispatch: Dispatch<AnyAction> = useDispatch();

  useEffect(
    () =>
      auth.onAuthStateChanged((user) => {
        if (user) {
          dispatch(setUserData({ id: user.uid, email: user.email }));
          navigation.navigate('Drawer');
        } else {
          dispatch(unsetUserData());
        }
      }),
    [navigation]
  );

  const handleSignUp = () => signUp(username);
  const handleOnChangeText = (text: string) => setUsername(text);
  const handleOnKeyPress = ({ nativeEvent }: { nativeEvent: TextInputKeyPressEventData }) => {
    if (nativeEvent.key === 'Enter') handleSignUp();
  };

  return children({ navigation, username, handleSignUp, handleOnChangeText, handleOnKeyPress });
};

const SignupContainerWrapper = () => (
  <SignupContainer>{(renderProps: any) => <SignUp {...renderProps} />}</SignupContainer>
);

export default SignupContainerWrapper;
