import React from 'react';
import { Dimensions, Platform } from 'react-native';
import { createDrawerNavigator, DrawerContentComponentProps } from '@react-navigation/drawer';
import { Feather } from '@expo/vector-icons';
import PasswordList from 'screens/PasswordList/passwordListContainer';
import CustomSidebar from 'components/CustomSidebar/customSidebarContainer';

const CustomDrawer = createDrawerNavigator();

export const Drawer = () => (
  <CustomDrawer.Navigator
    initialRouteName='PasswordList'
    screenOptions={{
      drawerStyle: {
        width: Platform.OS === 'web' ? 300 : Dimensions.get('window').width * 0.7,
      },
      drawerActiveBackgroundColor: '#9ac8fc',
      drawerActiveTintColor: '#3091e0',
      drawerItemStyle: {
        borderRadius: 5,
      },
    }}
    drawerContent={(sidebarProps: DrawerContentComponentProps) => (
      <CustomSidebar {...sidebarProps} />
    )}
  >
    <CustomDrawer.Screen
      name='PasswordList'
      component={PasswordList}
      options={{
        title: 'Password List',
        headerStyle: {
          backgroundColor: '#3091e0',
        },
        headerTintColor: '#FFF',
        drawerIcon: ({ color, size }) => <Feather name='list' size={size * 0.75} color={color} />,
      }}
    />
  </CustomDrawer.Navigator>
);
