import React, { Dispatch, useRef, useState } from 'react';
import { useDispatch } from 'react-redux';
import { AnyAction } from '@reduxjs/toolkit';
import { Animated, FlatList } from 'react-native';
import { useNavigation } from '@react-navigation/native';
import { NativeStackNavigationProp } from '@react-navigation/native-stack';
import { setIsRequest } from 'reduxStore/slices/applicationStatusSlice';
import { OnBoarding } from 'screens/OnBoarding/onBoarding';
import { slides } from 'screens/OnBoarding/slides';
import { setOnBoardingViewed } from 'utils/localStorageFuncs';

const OnBoardingContainer = ({ children }: { children: React.FunctionComponent }) => {
  const [currentIndex, setCurrentIndex] = useState<number>(0);
  const scrollX = useRef<Animated.Value>(new Animated.Value(0)).current;
  const slidesRef = useRef<FlatList>(null);
  const viewConfig = useRef({ viewAreaCoveragePercentThreshold: 50 }).current;
  const navigation: NativeStackNavigationProp<any> = useNavigation();
  const dispatch: Dispatch<AnyAction> = useDispatch();

  const viewableItemsChanged = useRef((item: { viewableItems: any }) => {
    const { viewableItems } = item;
    setCurrentIndex(viewableItems[0].index);
  }).current;

  const scrollTo = () => {
    if (currentIndex < slides.length - 1) {
      slidesRef.current?.scrollToIndex({ index: currentIndex + 1 });
    } else {
      setOnBoardingViewed('true');
      dispatch(setIsRequest());
      navigation.replace('Login');
    }
  };

  return children({
    currentIndex,
    scrollX,
    slidesRef,
    viewableItemsChanged,
    viewConfig,
    scrollTo,
  });
};

const OnBoardingContainerWrapper = () => (
  <OnBoardingContainer>{(renderProps: any) => <OnBoarding {...renderProps} />}</OnBoardingContainer>
);

export default OnBoardingContainerWrapper;
