import React, { useLayoutEffect } from 'react';
import { useSelector } from 'react-redux';
import { selectIsEditMode } from 'reduxStore/slices/passwordSlice';
import { selectUserId } from 'reduxStore/slices/userSlice';
import { useNavigation } from '@react-navigation/native';

const useGeneratorPageTitle = () => {
  const navigation = useNavigation();
  const userId = useSelector(selectUserId);
  const isEditMode = useSelector(selectIsEditMode);
  const screenTitle = isEditMode
    ? 'Password Edit'
    : `Password Generator ${!userId ? '(Only)' : ''}`;

  useLayoutEffect(() => {
    navigation.setOptions({
      title: screenTitle,
    });
  });

  return true;
};

export default useGeneratorPageTitle;
